import React from 'react'
import { navigateTo } from 'gatsby-link'
import 'semantic-ui-css/semantic.min.css'
import Login from '../../components/login.js'
import Simple from '../../components/simple.js'
import { Grid, Form, Container } from 'semantic-ui-react'

export default class extends React.Component {
  constructor (props) {
    super(props)
    this.state = { pass: '' }
  }
  render () {
    return (
      <Simple
        title='Welcome'
        description={
          <Container>
            <p>
              Hi, welcome to our interactive explorable on privacy and security.
              Today, we will teach you about the importance of having a strong password to secure your identity online.
            </p>
            <p>
              Enter a password that you can remember to begin
            </p>
          </Container>
        }
      >
        <Login onSubmit={e => { navigateTo(`/diagnostic/diagnostic?pass=${this.state.pass}`) }}>
          <Login.Password onChange={e => { this.setState({ pass: e.target.value }) }} />
          <Grid columns={2}>
            <Grid.Column width={4}>
              <Form.Button onClick={e => navigateTo(`/diagnostic/diagnostic?pass=${this.state.pass}`)}>
                Login
              </Form.Button>
            </Grid.Column>
          </Grid>
        </Login>
      </Simple>
    )
  }
}



